import React, { useState } from "react";
import "./Project.scss";
import { motion, AnimatePresence } from "framer-motion";
import EmailSection from "./EmailSection";
import movieImage from "../Images/Movie.png";
import expenseImage from "../Images/ExpensesTracker.png";
import learningSystem from "../Images/LearningSystem.png";
import cv from "../files/cv.pdf";

const variant = {
  init: { opacity: 0 },
  in: {
    opacity: 1,
    transition: {
      delay: 0.4,
    },
  },
  out: {
    opacity: 0,
    transition: {
      delay: 0.4,
    },
  },
};

const porjectList = [
  // {
  //   name: "",
  //   description:"",
  //   github: "",
  //   web: "",
  //   image: ,
  // },
  {
    name: "Expense tracker",
    description:
      "This is a web application to track incomes and expenses. I built this app using ReactJS for the front-end and Redux for the state management. To store all the data I linked the application to a MongoDB database using Express.",
    github: "https://github.com/julio-benavente/expense-tracker",
    web: "https://hidden-shore-83368.herokuapp.com/",
    image: expenseImage,
  },
  {
    name: "Learning Management System",
    description:
      "This website it's made to recreate an educational platform. Here I used ReactJS as the main framework, with this I made use of react-router-dom to display the views and framer-motion to make some animations. ",
    github: "https://github.com/julio-benavente/learningmangsys",
    web: "https://julio-benavente.github.io/learningmangsys/#/home",
    image: learningSystem,
  },
  {
    name: "Movie Theater",
    description:
      "This application is made with ReactJS. Here I displayed a selection of movies by category using The Movie Database API (TMDb API).",
    github: "https://github.com/julio-benavente/movies",
    web: "https://julio-benavente.github.io/movies/",
    image: movieImage,
  },
];

const ProjectEx = ({ name, description, github, web, image }) => {
  return (
    <div className="projectEx" style={{ backgroundImage: `url(${image})` }}>
      <div className="projectExWraper">
        <h3 className="projectName">{name}</h3>
        <p className="projectDescription">{description}</p>
        <a className="github" href={github} target="_blank">
          <span className="fab fa-github"></span>
        </a>
        <a className="globe" href={web} target="_blank">
          <span className="fas fa-globe"></span>
        </a>
      </div>
    </div>
  );
};

const Project = () => {
  const [displayEmail, setDisplayEmail] = useState(false);
  const handleEmail = () => setDisplayEmail(!displayEmail);

  return (
    <div className="projectSection" exit={{ opacity: 0 }}>
      <AnimatePresence>
        {displayEmail && <EmailSection handleEmail={handleEmail} />}
      </AnimatePresence>
      <motion.div
        variants={variant}
        initial="init"
        animate="in"
        exit="out"
        className="projectInformation"
      >
        <h1 className="projectTitle">Projects</h1>
        {porjectList.map((e, i) => {
          console.log(e);
          const { name, description, github, web, image } = e;
          return (
            <ProjectEx
              name={name}
              description={description}
              github={github}
              web={web}
              image={image}
            />
          );
        })}
      </motion.div>

      <motion.div
        variants={variant}
        initial="init"
        animate="in"
        exit="out"
        className="sideBar"
      >
        <span className="fas fa-envelope envelope" onClick={handleEmail}></span>
        <p className="cellphone">(+51) 934 - 898 - 005</p>

        <a href={cv} target="_blank">
          <span class="fas fa-file-download plus"></span>
        </a>
      </motion.div>
    </div>
  );
};

export default Project;
